.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: opacity 0.5s, visibility 0s linear 0.5s;
  cursor: pointer;
}
